body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: lightblue;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* My App Styles */
.App{
    background-color: white;
    max-width: 992px;
    margin: auto;
}

nav{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    border-bottom: 1px solid grey;
    background-color: orange;
    padding: 10px 20px;
}

.nav-menu li{
    display: inline-block;
    list-style: none;
}

.nav-menu__link{
    display: inline-block;
    margin: 5px;
    border: 1px solid grey;
    text-decoration: none;
    border-radius: 5px;
    padding: 3px 8px;
    background-color: white;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}

.nav-menu__link:hover{
    border: 1px solid black;
    background-color: rgb(228, 228, 228);
    cursor: pointer;
}

.nav-menu__link.active{
    background-color: royalblue;
    color: white;
}

.logo-link{
    color: white;
    text-decoration: none;
}

.back{
    background-color: lightblue;
    padding: 3px 8px;
    text-transform: uppercase;
    text-decoration: none;
    display: inline-block;
    border: 1px solid lightgray;
    border-radius: 5px;
    margin: 10px 0px;
}

/* Homepage Styles */
.main-content{
    width: 90%;
    margin: auto;
    padding-bottom: 50px;
}

.main-content h2{
    text-align: center;
    margin: 20px 0px;
}

.home-image{
    width: 100%;
    height: 500px;
    background-image: url(http://www.fillmurray.com/800/500);
    background-size: cover;
    margin-bottom: 50px;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    background-position: 50% 50%;
}

.home-image:hover{
    background-image: url(http://www.fillmurray.com/806/500);
    cursor: pointer;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-transition: all .3s ease-in-out;
}

.thumbnail-container{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    margin-bottom: 50px;
    width: 100%;
}

.home-thumbnail-1,
.home-thumbnail-2,
.home-thumbnail-3{
    background-size: cover;
    width: 200px;
    height: 100px;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}

.home-thumbnail-1:hover,
.home-thumbnail-2:hover,
.home-thumbnail-3:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    cursor: pointer;
}

.home-thumbnail-1{
    background-image: url(http://www.fillmurray.com/810/500);
}

.home-thumbnail-2{
    background-image: url(http://www.fillmurray.com/801/500);
}

.home-thumbnail-3{
    background-image: url(http://www.fillmurray.com/802/500);
}


/* Actor Page Styles */
.container{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row wrap;
            flex-flow: row wrap;
    margin: auto;
}

.actor-container{
    display: inline-block;
    text-align: center;
}

.actor-image{
    width: 250px;
    height: 250px;
    background-size: cover;
}

/* Single Actor Styles */
.image-profile{
    width: 100%;
    height: 500px;
    background-size: cover;
}